import { Component, OnDestroy, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { QueryRef } from 'apollo-angular';
import { NzMessageService } from 'ng-zorro-antd/message';
import { Subject, Subscription } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import {
  GetPostCommentReportListGQL,
  GetPostCommentReportListQuery,
  GetPostCommentReportListQueryVariables,
  OrderBy,
  PostCommentReportFieldFragment,
} from 'src/graphql/graphql';

enum Sort {
  ID_ASC = 'ID_ASC',
  ID_DESC = 'ID_DESC',
}

@Component({
  selector: 'app-post-report-list',
  templateUrl: './post-report-list.component.html',
  styleUrls: ['./post-report-list.component.scss'],
})
export class PostReportListComponent implements OnInit, OnDestroy {
  searchForm: FormGroup;
  loading = false;
  reportsCount = 0;
  pageIndex = 1;
  pageSize = 10;
  destroy$: Subject<boolean> = new Subject();
  sort = Sort.ID_DESC;
  Sort = Sort;
  dataSubscription?: Subscription;
  dataQueryRef?: QueryRef<
    GetPostCommentReportListQuery,
    GetPostCommentReportListQueryVariables
  >;
  reports: PostCommentReportFieldFragment[] = [];

  reportType: 'comment' | 'post' = 'comment';

  constructor(
    private fb: FormBuilder,
    private router: Router,
    private route: ActivatedRoute,
    private msgService: NzMessageService,
    private translateService: TranslateService,
    private getPostCommentReportGQL: GetPostCommentReportListGQL,
  ) {
    this.searchForm = this.fb.group({
      sort: [Sort.ID_DESC],
    });
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.unsubscribe();
  }

  ngOnInit(): void {
    this.route.queryParamMap
      .pipe(takeUntil(this.destroy$))
      .subscribe((paramMap) => {
        this.pageIndex = parseInt(paramMap.get('page') || '1', 10);
        this.pageSize = parseInt(paramMap.get('size') || '10', 10);
        this.sort = Object.values(Sort).includes(paramMap.get('sort') as Sort)
          ? (paramMap.get('sort') as Sort)
          : Sort.ID_DESC;
        if (this.dataSubscription) {
          this.refetch();
        } else {
          this.dataQueryRef = this.getPostCommentReportGQL.watch(
            this.getVariables(),
            {
              notifyOnNetworkStatusChange: true,
              fetchPolicy: 'network-only',
            },
          );

          this.dataSubscription = this.dataQueryRef.valueChanges
            .pipe(takeUntil(this.destroy$))
            .subscribe(
              ({ data, loading, error, errors }) => {
                this.loading = loading;

                if (loading) {
                  return;
                }
                if (error || errors) {
                  this.msgService.error(
                    this.translateService.instant(
                      'ui.message.can_not_load_data',
                    ),
                  );
                  return;
                }
                if (data) {
                  this.reports = data.postCommentReports;
                  this.reportsCount =
                    data.postCommentReportsConnection.aggregate.count;
                }
              },
              () => {
                this.msgService.error(
                  this.translateService.instant('ui.message.can_not_load_data'),
                );
              },
            );
        }
      });
  }
  private getVariables(): GetPostCommentReportListQueryVariables {
    return {
      first: this.pageSize,
      skip: (this.pageIndex - 1) * this.pageSize,
      orderBy: {
        id: this.sort === Sort.ID_DESC ? OrderBy.Desc : OrderBy.Asc,
      },
    };
  }
  refetch(): void {
    this.dataQueryRef?.refetch(this.getVariables());
  }

  handleSearch(): void {
    this.router.navigate([], {
      queryParams: {
        sort: this.searchForm.get('sort')?.value || undefined,
      },
      relativeTo: this.route,
      queryParamsHandling: 'merge',
    });
  }

  paginate(page: number): void {
    this.router.navigate([], {
      queryParams: {
        page,
      },
      relativeTo: this.route,
      queryParamsHandling: 'merge',
    });
  }
}
