import { Component, OnDestroy, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { QueryRef } from 'apollo-angular';
import dayjs from 'dayjs';
import { NzDateMode } from 'ng-zorro-antd/date-picker';
import { NzMessageService } from 'ng-zorro-antd/message';
import { Subject, Subscription } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

import { environment } from 'src/environments/environment';

import {
  GetVideoPostListGQL,
  GetVideoPostListQuery,
  GetVideoPostListQueryVariables,
  PostStatus,
  VideoStatus,
} from 'src/graphql/graphql';

type FilterVideoStatus = PostStatus | 'all';

@Component({
  selector: 'app-video-list',
  templateUrl: './video-list.component.html',
  styleUrls: ['./video-list.component.scss'],
})
export class VideoListComponent implements OnInit, OnDestroy {
  PostStatus = PostStatus;
  pickerMode: NzDateMode = 'date';
  listVideoStatus: FilterVideoStatus = 'all';
  totalVideo = 0;
  reportsCount = 0;
  pageIndex = 1;
  pageSize = 12;
  totalListVideo = 0;
  posts!: GetVideoPostListQuery['posts'];
  destroy$: Subject<boolean> = new Subject();
  searchForm: FormGroup;
  text?: string;
  dataSubscription?: Subscription;
  dataQueryRef?: QueryRef<
    GetVideoPostListQuery,
    GetVideoPostListQueryVariables
  >;
  loading = false;
  period: Array<Date | null> = [null, null];

  constructor(
    private getVideoPostList: GetVideoPostListGQL,
    private fb: FormBuilder,
    private route: ActivatedRoute,
    private router: Router,
    private msgService: NzMessageService,
    private translateService: TranslateService,
  ) {
    this.searchForm = this.fb.group({ period: [null, null] });
  }

  ngOnInit(): void {
    this.route.queryParamMap
      .pipe(takeUntil(this.destroy$))
      .subscribe((paramMap) => {
        this.pageIndex = parseInt(paramMap.get('page') || '1', 10);
        this.pageSize = parseInt(paramMap.get('size') || '12', 10);
        this.text = paramMap.get('text') || '';

        const videoStatus = paramMap.get('videoStatus') as PostStatus;

        this.listVideoStatus = Object.values(PostStatus).includes(videoStatus)
          ? videoStatus
          : 'all';

        this.period = [
          (parseInt(paramMap.get('started_at') || '') &&
            new Date(parseInt(paramMap.get('started_at')!))) ||
            null,
          (parseInt(paramMap.get('ended_at') || '') &&
            new Date(parseInt(paramMap.get('ended_at')!))) ||
            null,
        ];

        this.patchFormValue();

        if (this.dataSubscription) {
          this.refetch();
        } else {
          this.dataQueryRef = this.getVideoPostList.watch(this.getVariables(), {
            notifyOnNetworkStatusChange: true,
            fetchPolicy: 'network-only',
          });
          this.dataSubscription = this.dataQueryRef.valueChanges
            .pipe(takeUntil(this.destroy$))
            .subscribe(
              ({ data, loading, error, errors }) => {
                this.loading = loading;
                if (loading) {
                  return;
                }
                if (error || errors) {
                  this.msgService.error(
                    this.translateService.instant(
                      'ui.message.can_not_load_data',
                    ),
                  );
                  return;
                }
                if (data) {
                  this.posts = data.posts;
                  this.totalVideo = data.postsConnection.aggregate.count;
                }
              },
              () => {
                this.msgService.error(
                  this.translateService.instant('ui.message.can_not_load_data'),
                );
              },
            );
        }
      });
  }

  patchFormValue(): void {
    if (
      this.period[0] &&
      this.period[1] &&
      this.searchForm.controls.period.untouched
    ) {
      this.searchForm.patchValue({
        period: this.period,
      });
    }
  }

  refetch(): void {
    this.dataQueryRef?.refetch(this.getVariables());
  }

  private getVariables(): GetVideoPostListQueryVariables {
    let videoStatus;
    if (this.listVideoStatus !== 'all') {
      videoStatus = this.listVideoStatus;
    }
    return {
      where: {
        category: { id: environment.videoCategoryId },
        status: videoStatus,
        createdAtGte: this.period?.[0] || undefined,
        createdAtLte: this.period?.[1] || undefined,
      },
      first: this.pageSize,
      skip: (this.pageIndex - 1) * this.pageSize,
    };
  }

  handleSearch() {
    const searchData = this.searchForm.value;

    const [startedAt, endedAt] = this.parseDateRange(
      searchData.period,
      this.pickerMode,
    );
    this.pageIndex = 1;
    this.router.navigate([], {
      queryParams: {
        page: this.pageIndex,
        started_at: startedAt?.getTime(),
        ended_at: endedAt?.getTime(),
      },
      relativeTo: this.route,
      queryParamsHandling: 'merge',
    });
  }

  parseDateRange(
    range: Array<Date | null>,
    mode: NzDateMode,
  ): Array<Date | null> {
    const length = range.length;
    switch (mode) {
      case 'date': {
        return range;
      }
      case 'month': {
        const monthRange = range.map((date, index) => {
          if (!date) {
            return date;
          }
          const dayObject = dayjs(date);
          if (index !== length - 1) {
            return dayObject.startOf('week').toDate();
          }
          return dayObject.endOf('week').toDate();
        });
        return monthRange;
      }
      case 'year': {
        const yearnRange = range.map((date, index) => {
          if (!date) {
            return date;
          }
          const dayObject = dayjs(date);
          if (index !== length - 1) {
            return dayObject.startOf('year').toDate();
          }
          return dayObject.endOf('year').toDate();
        });
        return yearnRange;
      }
      default:
        return range;
    }
  }

  changeFilterStatus(status: FilterVideoStatus) {
    this.pageIndex = 1;
    this.router.navigate([], {
      queryParams: {
        page: this.pageIndex,
        videoStatus: status,
      },
      relativeTo: this.route,
      queryParamsHandling: 'merge',
    });
  }

  paginate(page: number) {
    this.router.navigate([], {
      queryParams: {
        page,
      },
      relativeTo: this.route,
      queryParamsHandling: 'merge',
    });
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.unsubscribe();
  }
}
