import { Component, OnDestroy, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { QueryRef } from 'apollo-angular';
import { NzMessageService } from 'ng-zorro-antd/message';
import { Subject, Subscription } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

import {
  GetPostListGQL,
  GetPostListQuery,
  GetPostListQueryVariables,
  OrderBy,
  PostCardFragment,
  PostStatus,
  SearchUsersGQL,
  User,
} from '../../../../graphql/graphql';

enum Sort {
  UPDATED_AT_ASC = 'UPDATED_AT_ASC',
  UPDATED_AT_DESC = 'UPDATED_AT_DESC',
}

@Component({
  selector: 'app-post-list',
  templateUrl: './post-list.component.html',
  styleUrls: ['./post-list.component.scss'],
})
export class PostListComponent implements OnInit, OnDestroy {
  PostStatus = PostStatus;
  Sort = Sort;
  destroy$: Subject<boolean> = new Subject();
  posts: Array<PostCardFragment> = [];
  postsCount = 0;
  pageIndex = 1;
  pageSize = 10;
  loading = false;
  dataQueryRef:
    | QueryRef<GetPostListQuery, GetPostListQueryVariables>
    | undefined;
  dataSubscription: Subscription | undefined;

  searchForm: FormGroup;
  text?: string;
  status?: PostStatus;
  isDeleted? = false;
  sort = Sort.UPDATED_AT_DESC;
  userId?: User['id'];
  period: Array<Date | null> = [null, null];
  isMobile?: boolean;

  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private fb: FormBuilder,
    private getPostListGQL: GetPostListGQL,
    private searchUsersGQL: SearchUsersGQL,
    private msgService: NzMessageService,
    private translateService: TranslateService,
  ) {
    this.searchForm = this.fb.group({
      text: [null],
      status: ['ALL'],
      isDeleted: [false],
      sort: [Sort.UPDATED_AT_DESC],
      user: [undefined],
      period: [null, null],
    });
  }

  ngOnInit(): void {
    this.route.queryParamMap
      .pipe(takeUntil(this.destroy$))
      .subscribe((paramMap) => {
        this.pageIndex = parseInt(paramMap.get('page') || '1', 10);
        this.pageSize = parseInt(paramMap.get('size') || '10', 10);
        this.text = paramMap.get('text') || '';
        this.userId = parseInt(paramMap.get('user') || '0', 10) || undefined;
        this.status = Object.values(PostStatus).includes(
          paramMap.get('status') as PostStatus,
        )
          ? (paramMap.get('status') as PostStatus)
          : undefined;
        this.sort = Object.values(Sort).includes(paramMap.get('sort') as Sort)
          ? (paramMap.get('sort') as Sort)
          : Sort.UPDATED_AT_DESC;
        this.isDeleted =
          paramMap.get('deleted') === 'true'
            ? true
            : paramMap.get('deleted') === 'false'
            ? false
            : undefined;
        this.period = [
          (!isNaN(parseInt(paramMap.get('started_at') || '')) &&
            new Date(parseInt(paramMap.get('started_at')!))) ||
            null,
          (!isNaN(parseInt(paramMap.get('ended_at') || '')) &&
            new Date(parseInt(paramMap.get('ended_at')!))) ||
            null,
        ];

        this.patchFormValue();

        if (this.dataSubscription) {
          this.refetch();
        } else {
          this.dataQueryRef = this.getPostListGQL.watch(this.getVariables(), {
            notifyOnNetworkStatusChange: true,
            fetchPolicy: 'network-only',
          });
          this.dataSubscription = this.dataQueryRef.valueChanges
            .pipe(takeUntil(this.destroy$))
            .subscribe(
              ({ data, loading, error, errors }) => {
                this.loading = loading;
                if (loading) {
                  return;
                }

                if (error || errors) {
                  this.msgService.error(
                    this.translateService.instant(
                      'ui.message.can_not_load_data',
                    ),
                  );
                  return;
                }

                if (data) {
                  this.posts = data.posts;
                  this.postsCount = data.postsConnection.aggregate.count;
                }
              },
              () => {
                this.msgService.error(
                  this.translateService.instant('ui.message.can_not_load_data'),
                );
              },
            );
        }
      });
  }

  patchFormValue(): void {
    /**
     * FIXME nz-select does not support undefined and null
     * https://github.com/NG-ZORRO/ng-zorro-antd/issues/5950
     */
    if (this.searchForm.controls.isDeleted.untouched) {
      this.searchForm.patchValue({
        isDeleted: typeof this.isDeleted === 'boolean' ? this.isDeleted : 'ALL',
      });
    }

    if (this.searchForm.controls.status.untouched) {
      this.searchForm.patchValue({
        status: this.status || 'ALL',
      });
    }

    if (this.text?.trim() && this.searchForm.controls.product.untouched) {
      this.searchForm.patchValue({
        text: this.text,
      });
    }

    if (Sort[this.sort]) {
      this.searchForm.patchValue({
        sort: this.sort || undefined,
      });
    }

    /**
     * FIXME nz-select untouched = true event user select a value
     */
    if (
      this.userId &&
      this.searchForm.controls.user.untouched &&
      this.searchForm.controls.user.pristine
    ) {
      this.searchUsersGQL
        .fetch({
          where: {
            id: this.userId,
          },
          first: 1,
        })
        .pipe(takeUntil(this.destroy$))
        .subscribe(({ data }) => {
          if (data)
            this.searchForm.patchValue({
              user: data.users[0],
            });
        });
    }

    if (
      this.period[0] &&
      this.period[1] &&
      this.searchForm.controls.period.untouched
    ) {
      this.searchForm.patchValue({
        period: this.period,
      });
    }
  }

  refetch(): void {
    this.dataQueryRef?.refetch(this.getVariables());
  }

  private getVariables(): GetPostListQueryVariables {
    return {
      where: {
        contentContains: this.text?.trim() || undefined,
        status: this.status || undefined,
        author: {
          id: this.userId || undefined,
        },
        createdAtGte: this.period?.[0] || undefined,
        createdAtLte: this.period?.[1] || undefined,
      },
      first: this.pageSize,
      skip: (this.pageIndex - 1) * this.pageSize,
      orderBy: [
        {
          commentedAt: OrderBy.Desc,
        },
        {
          id: OrderBy.Desc,
        },
      ],
    };
  }

  paginate(page: number): void {
    this.router.navigate([], {
      queryParams: {
        page,
      },
      relativeTo: this.route,
      queryParamsHandling: 'merge',
    });
  }

  handleSearch(): void {
    const searchData = this.searchForm.value;
    const text = searchData.text || undefined;
    const sort = searchData.sort || undefined;
    const isDeleted =
      typeof searchData.isDeleted === 'boolean'
        ? searchData.isDeleted
        : undefined;
    const status = Object.values(PostStatus).includes(searchData.status)
      ? searchData.status
      : undefined;
    const userId = searchData.user?.id;
    const startedAt = searchData.period?.[0]?.getTime() || undefined;
    const endedAt = searchData.period?.[1]?.getTime() || undefined;

    this.router.navigate([], {
      queryParams: {
        text,
        sort,
        deleted: isDeleted,
        status,
        user: userId,
        page: 1,
        started_at: startedAt,
        ended_at: endedAt,
      },
      relativeTo: this.route,
      queryParamsHandling: 'merge',
    });
  }

  trackById(index: number, item: any): number {
    return item.id;
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.unsubscribe();
  }
}
