import {
  AfterViewInit,
  Component,
  Input,
  OnDestroy,
  OnInit,
} from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import dayjs, { Dayjs } from 'dayjs';
import { NzMessageService } from 'ng-zorro-antd/message';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

import {
  ApprovePostGQL,
  Gender,
  PostCardFragment,
  PostStatus,
  RejectPostGQL,
} from '../../../../graphql/graphql';

@Component({
  selector: 'app-post-card',
  templateUrl: './post-card.component.html',
  styleUrls: ['./post-card.component.scss'],
})
export class PostCardComponent implements OnInit, OnDestroy, AfterViewInit {
  PostStatus = PostStatus;
  Gender = Gender;
  destroy$: Subject<boolean> = new Subject();
  now: Dayjs = dayjs();

  @Input()
  post!: PostCardFragment;
  status!: PostStatus;

  rejecting = false;
  approving = false;

  constructor(
    private approvePostGQL: ApprovePostGQL,
    private rejectPostGQL: RejectPostGQL,
    private msgService: NzMessageService,
    private translateService: TranslateService,
  ) {}

  ngOnInit(): void {
    this.status = this.post.status;
  }

  ngAfterViewInit(): void {
    if (this.post.images?.length) {
      $(`#post_gallery_${this.post.id}`)
        .justifiedGallery()
        .on('jg.complete', () => {
          lightGallery(
            document.getElementById(`post_gallery_${this.post.id}`),
            {
              thumbnail: true,
            },
          );
        });
    }
  }

  approve(): void {
    this.approving = true;
    this.approvePostGQL
      .mutate({
        id: this.post.id,
      })
      .pipe(takeUntil(this.destroy$))
      .subscribe(
        ({ data, errors }) => {
          this.approving = false;
          if (errors) {
            this.msgService.error(
              this.translateService.instant('review.approve_error'),
            );
            return;
          }

          if (data) {
            this.status = PostStatus.Approved;
            this.msgService.success(
              this.translateService.instant('review.approve_success'),
            );
          }
        },
        () => {
          this.approving = false;
        },
      );
  }

  reject(): void {
    this.rejecting = true;
    this.rejectPostGQL
      .mutate({
        id: this.post.id,
      })
      .pipe(takeUntil(this.destroy$))
      .subscribe(
        ({ data, errors }) => {
          this.rejecting = false;
          if (errors) {
            this.msgService.error(
              this.translateService.instant('review.hide_error'),
            );
            return;
          }

          if (data) {
            this.status = PostStatus.Hidden;
            this.msgService.success(
              this.translateService.instant('review.hide_success'),
            );
          }
        },
        () => {
          this.rejecting = false;
        },
      );
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.unsubscribe();
    if (this.post.images?.length) {
      $(`#post_gallery_${this.post.id}`).justifiedGallery('destroy');
      (window as any).lgData[
        document
          .getElementById(`post_gallery_${this.post.id}`)
          ?.getAttribute('lg-uid') || ''
      ]?.destroy(true);
    }
  }
}
