import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { NzMessageService } from 'ng-zorro-antd/message';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

import {
  ApprovePostGQL,
  PostStatus,
  RejectPostGQL,
  VideoCardFragment,
} from 'src/graphql/graphql';

@Component({
  selector: 'app-video-card',
  templateUrl: './video-card.component.html',
  styleUrls: ['./video-card.component.scss'],
})
export class VideoCardComponent implements OnInit, OnDestroy {
  @Input()
  post!: VideoCardFragment;
  PostStatus = PostStatus;
  status!: PostStatus;
  destroy$: Subject<boolean> = new Subject();
  approving = false;
  rejecting = false;

  constructor(
    private msgService: NzMessageService,
    private translateService: TranslateService,
    private rejectPostGQL: RejectPostGQL,
    private approvePostGQL: ApprovePostGQL,
  ) {}

  ngOnInit(): void {
    this.status = this.post.status;
  }

  approve(): void {
    this.approving = true;
    this.approvePostGQL
      .mutate({
        id: this.post.id,
      })
      .pipe(takeUntil(this.destroy$))
      .subscribe(
        ({ data, errors }) => {
          if (errors) {
            this.msgService.error(
              this.translateService.instant('post.approve_video_error'),
            );
            return;
          }

          if (data) {
            this.status = PostStatus.Approved;
            this.msgService.success(
              this.translateService.instant('post.approve_video_success'),
            );
          }
        },
        () => {
          this.approving = false;
        },
      );
  }

  reject(): void {
    this.rejectPostGQL
      .mutate({
        id: this.post.id,
      })
      .pipe(takeUntil(this.destroy$))
      .subscribe(
        ({ data, errors }) => {
          if (errors) {
            this.msgService.error(
              this.translateService.instant('post.reject_video_error'),
            );
            return;
          }

          if (data) {
            this.status = PostStatus.Hidden;
            this.msgService.success(
              this.translateService.instant('post.reject_video_success'),
            );
          }
        },
        () => {
          this.rejecting = false;
        },
      );
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.unsubscribe();
  }
}
