import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { NzMessageService } from 'ng-zorro-antd/message';

import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import {
  ApprovePostCommentReportGQL,
  PostCommentReportFieldFragment,
  PostCommentStatus,
  RejectPostCommentReportGQL,
} from 'src/graphql/graphql';

@Component({
  selector: 'app-post-comment-report-card',
  templateUrl: './post-comment-report-card.component.html',
  styleUrls: ['./post-comment-report-card.component.scss'],
})
export class PostCommentReportCardComponent implements OnInit, OnDestroy {
  private destroy$: Subject<boolean> = new Subject();
  PostCommentStatus = PostCommentStatus;
  @Input()
  report!: PostCommentReportFieldFragment;
  status!: PostCommentStatus;

  approving = false;
  rejecting = false;
  constructor(
    private approvePostCommentReportGQL: ApprovePostCommentReportGQL,
    private rejectPostCommentReportGQL: RejectPostCommentReportGQL,
    private msgService: NzMessageService,
    private translateService: TranslateService,
  ) {}

  ngOnInit(): void {
    this.status = this.report.postComment.status;
  }

  approve(): void {
    this.approving = true;
    this.approvePostCommentReportGQL
      .mutate({
        where: {
          id: this.report.id,
        },
      })
      .pipe(takeUntil(this.destroy$))
      .subscribe(
        ({ data, errors }) => {
          this.approving = false;
          if (errors) {
            this.msgService.error(
              this.translateService.instant('report.approve_report_error'),
            );
            return;
          }

          if (data) {
            this.status = PostCommentStatus.Hidden;
            this.msgService.success(
              this.translateService.instant('report.approve_report_success'),
            );
          }
        },
        () => {
          this.approving = false;
        },
      );
  }

  reject(): void {
    this.rejecting = true;
    this.rejectPostCommentReportGQL
      .mutate({
        where: {
          id: this.report.id,
        },
      })
      .pipe(takeUntil(this.destroy$))
      .subscribe(
        ({ data, errors }) => {
          this.rejecting = false;
          if (errors) {
            this.msgService.error(
              this.translateService.instant('report.reject_report_error'),
            );
            return;
          }

          if (data) {
            this.status = PostCommentStatus.Reported;
            this.msgService.success(
              this.translateService.instant('report.reject_report_success'),
            );
          }
        },
        () => {
          this.rejecting = false;
        },
      );
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.unsubscribe();
  }
}
